@import "../../styles/variables";
@import "../../styles/rwd";

.banner-comp {
  background: url("../../assets/gifs/bannerBg.gif") no-repeat center;
  background-size: cover;
  position: relative;
  min-height: 100vh;

  &-blur-bg {
    background-color: rgba(0, 0, 0, .5);
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
  }

  &-container {
    min-height: calc(100vh - 61px);
    position: relative;
  }
}

.banner-comp-container .banner-comp {
  &-title, &-subtitle, &-btn, &-scroll {
    z-index: 1;
  }

  &-content {
    @include mobile {
      margin-bottom: 42px;
    }
  }

  &-title, &-subtitle h4 {
    color: #ffffff;
    font-family: Android7, sans-serif !important;
  }

  &-title {
    font-size: 80px;
    text-align: center;
    height: 250px;
    @include mobile {
      height: 100px;
      font-size: 55px;
    }
    @include tablet {
      height: 150px;
    }
  }

  &-subtitle {
    h4 {
      text-align: center;
      font-size: 22px;
      line-height: 30px;

      &:last-child {
        margin-top: 12px;
        font-size: 13px;
        line-height: 18px;
      }

      @include smallScreen {
      }
      @include mobile {
      }
      @include tablet {
        font-size: 20px;
      }
    }

    @include mobile {
      margin: 12px 0;
    }
  }

  &-scroll {
    width: 80px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);

    img {
      width: 100%;
    }
  }
}